	module.exports = {"data":{"markdownRemark":{"html":"<p>ある日の晩、○○は神社の縁側に腰掛け、星を見上げては呆けていた。霊夢は入浴中、彼女の長風呂は幻想郷でも有名なほどの長さだ。入浴待ちってのもあるが、この退屈な時間を活用してそろそろやっておくべきことがある。 そして子の刻になる頃、西から東からアリス、魔理沙、パチュリーの3人がやって来た。 ○「やあ、本当に時間通りに来てくれたね」 ア「何？今日はあなたが宴会の幹事になるのかしら？」 パ「そういえば霊夢は？」 ○「霊夢は入浴中。宴会も悪くないが今日はもっと大事な用件だからパスだ。実は……その、これから付き合いたい人を決めようと思ってね」 それを聞き3人の目の色が変わる。 ○「たった今から椅子取りゲームの要領で僕の膝の上を占領できた子と付き合おうと思う」 パ「思ったより簡単なのね」 ○「あぁ手っ取り早くて分かりやすいだろ。さぁもう勝負は始まってる、かかってこい」 ○○がひときわ強く声を上げると合図されたかのように彼の元に突っ込んでいく3人。 ○「おー、こりゃ魔理沙が一番早いか……っておい待てちょっとそれ軌道がおかしくｎﾌﾟｷﾞｬ！」 魔「……あのなぁ、なんだよこの決め方。私は何となくむかついたぜ」 ○「ﾑｷﾞｭ・・・」 魔理沙のヒップアタックをもろに受け仰向けに倒れた○○の顔の上を一人占領し毒づく魔理沙。 魔「……あとお前らももうちょっと怒れよこいつに」 魔理沙が顔を上げると○○の膝の上でおしくらまんじゅうしてるアリパチェ。 霊「あー、襖壊したわね！○○さん後で体で弁償ね」 ○「ﾑｸﾞ……ﾓｺﾞ……あぁ霊夢、実はお前が好きだ付き合ってくれないｋ」 魔「但し魔砲は尻から出るスパーク！！！」</p>","frontmatter":{"cover":null,"date":"2005-09-07T01:58:26+09:00","strDate":"2005/9/6","title":"本文"}}},"pathContext":{"path":"/ja/blog/2005/09/07-本文-5/","langKey":"ja"}}

